@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}
/* 透明渐变 - color - transparent */
.css_hr_color_to_transparent {
  margin: 2;
  padding: 0;
  border: 0;
  height: 1px;
  background-image: -webkit-gradient(linear, left top, right top, from(rgba(250, 243, 243, 0)), color-stop(rgba(255, 255, 255, 0.75)), to(rgba(0, 0, 0, 0)));
  background-image: -webkit-linear-gradient(left, rgba(250, 243, 243, 0), rgba(255, 255, 255, 0.75), rgba(0, 0, 0, 0));
  background-image: -o-linear-gradient(left, rgba(250, 243, 243, 0), rgba(255, 255, 255, 0.75), rgba(0, 0, 0, 0));
  background-image: linear-gradient(to right, rgba(250, 243, 243, 0), rgba(255, 255, 255, 0.75), rgba(0, 0, 0, 0));
}


